body, html {
  height: 100%;
  background-image: url("stripe_1.png");
  background-repeat: repeat-y repeat-x;
}

.main {
padding-top: 20px;
}

.kinder-brand{
font-size: 30px;
}

body{
font-family: arial;
margin: 0;
}
nav{
background: #ff5353 ;
padding: 10px 10px;
}
nav ul{
max-width: 960px;
margin: 0 auto;
padding: 0;
}
nav li{
list-style-type: none;
display: inline-block;
margin: 0 10px 0 0;
}
nav a{
color: #fff;
font-size: 18px;
background: rgba(255,255,255,0);
text-decoration: none;
padding: 10px;
display: block;
}
main, header{
max-width: 960px; 
margin: 30px auto;
padding: 0 10px;
color: #333;
}



